.carousel {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    transition: transform .350s ease-in-out;
    position: relative;
}

.breadcrumbs {
    display: flex;
    justify-content: center;
    align-items: center;
    bottom: 4vh;
    position: absolute;
    width: 100%;
}
